import { Link, useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"
import React from "react"

import ScrollAnimation from "react-animate-on-scroll"

import './previewCard.scss'

const PreviewCard = (props) => {

    const imgData = useStaticQuery(graphql`
    query {
        images: allFile {
          edges {
            node {
              relativePath
              name
              childImageSharp {
                fluid(traceSVG: {threshold: 190, background: "#FFFFFF", color: "#C8CDE1"}, pngQuality: 85,  maxWidth: 600) {
                    ...GatsbyImageSharpFluid_tracedSVG
                }
              }
            }
          }
        }
      }
    `)

    return (
        <ScrollAnimation className="project" animateIn="previewCardAnimation" animateOnce={true}>
            <div className="project__img" style={{
                boxShadow: `-6px 12px 18px -8px ${props.shadowColor}`
            }}>
                {
                    (() => {
                        if (props.projectLink) {
                            return (
                                <Link to={props.projectLink}>
                                    <Img fluid={imgData.images.edges.find(img => img.node.relativePath === props.imgName).node.childImageSharp.fluid} 
                                        style={{
                                            height:"100%"
                                        }}
                                        objectFit="cover"
                                        objectPosition="50% 50%"/>
                                </Link>
                            )
                        } else {
                            return (
                                <Img fluid={imgData.images.edges.find(img => img.node.relativePath === props.imgName).node.childImageSharp.fluid} 
                                style={{
                                    height:"100%"
                                }}
                                objectFit="cover"
                                objectPosition="50% 50%"/>
                            )
                        }
                    })()
                }
            </div>
            <div className="project__content">
                <h1 
                    className="project__title" 
                    style={{
                        color: props.themeColor,
                    }}>
                    {props.title}
                </h1>
                <small className="project__tags" style={{color: props.darkColor}}>
                {
                    props.tags.map((tag, i) => {
                        if (i === props.tags.length - 1)
                            return tag
                        else
                            return tag + " · "
                    })
                }
                </small>
                <p className="project__description" style={{color: props.textColor}}>
                    {props.description}
                </p>
                <div className="project__CTAs">
                {
                    props.liveDemoLink !== undefined &&
                        <a
                            href={props.liveDemoLink}
                            className={`project__CTA rectBtn--${props.color}`}
                            >
                            {props.liveDemoText}
                        </a>
                }
                {
                    props.projectLink !== undefined &&
                        <Link 
                            to={props.projectLink}
                            className={`project__CTA rectBtn--${props.color}`}
                        >
                            Learn More
                        </Link>
                }
                </div>
            </div>
        </ScrollAnimation>
    )

}

export default PreviewCard