import React from 'react'
import { Link, useStaticQuery, graphql } from 'gatsby'
import Img from "gatsby-image/withIEPolyfill"
import "./hero.scss"

const Hero = (props) => {

    const imgData = useStaticQuery(graphql`
        query {
            placeholderImage: file(relativePath: {eq: "hyper_cube.png"}) {
                childImageSharp {
                    fluid(traceSVG: {threshold: 185, color: "#a9b4df"}) {
                        ...GatsbyImageSharpFluid_tracedSVG
                    }
                }
            }
        }      
    `)

    return (
        <>
            <svg
                stroke="black"
                fill="transparent"
                className="bgSVG"
                viewBox="0 0 37 37"
                width="36.35rem"
                height="36.35rem"
                xmlns="http://www.w3.org/2000/svg">
                <circle
                    className="bgSVG__circle"
                    cx="50%" cy="50%" r="18"/>
            </svg>
            <section
                className="heroContent">
                <h2
                    className="heroContent__name">
                    Bernard Fok
                </h2>
                <h1
                    className="heroContent__tagline">
                    A Web Dev Enthusiast who make ideas come true
                </h1>
                <Link
                    className="heroContent__CTA rectBtn--blue"
                    to="/#projects">
                        View Projects
                </Link>
                <div className="heroContent__img">
                    <Img fluid={imgData.placeholderImage.childImageSharp.fluid} 
                        objectFit="cover"
                        objectPosition="50% 50%"/>
                </div>
            </section>
        </>
    )
}

export default Hero
